.footer-body{
    width: 100%;
    height: 25vh;
    background: rgb(31, 30, 30);
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer-name{
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    color: #fff;

}
/* .footer-contacts{
    color: #fff;
    position: absolute;
    left: 45%;
    padding-top: 8rem;
} */

a{
    color: #fff;
    font-size: 1rem;
}

.footer-menu {
    position: absolute;
    transform: translate(-50%,-50%);
    /* top: 420%; */
    padding-top: 3rem;
    left: 50%;
    font-size: 1rem;
}

.footer-menu ul {
    display: flex;
    font-weight: bolder
}


/* css for light theme */
.light .footer-body{
background-color: #d5ebee;
}

.light a{
    color: #000;
}




@media screen and (max-width:768px){
    .footer-body{
        width: 100%;
        height: 20vh;
        margin-top: 200%;
    }

    .footer-name{
        position: absolute;
        transform: translate(-50%, -50%);
        left: 50%;
        font-size: 0.7rem;
        color: #fff;
    
    }
    .footer-menu {
        position: absolute;
        /* transform: translate(-50%,-50%); */
        padding-top: 3rem;
        left: 50%;
        font-size: 1rem;
    }


}