.projects{
    width: 100vh;
    height: 100vh;
}

.projects .pro-title{
    font-size: 1.5rem;
    text-align: center;
    position: absolute;
    margin-left: 40%;
    /* margin-top: 100px; */
    margin-top: 5%;
    }

.img-card1{
    width: 390px;
    height: 180px;
    position: absolute;
    margin-top: 20%;
    margin-left: 50px;
    background-color: #fff;

}

.img-card1 img{
    margin-top: 5px;
    margin-left: 5px;
    width: 380px;
    height: 170px;
}

.img-card2{
    width: 390px;
    height: 180px;
    position: absolute;
    margin-top: 20%;
    margin-left: 500px;
    background-color: #fff;
}

.img-card2 img{
    margin-top: 5px;
    margin-left: 5px;
    width: 380px;
    height: 170px;
}

.img-card3{
    width: 390px;
    height: 180px;
    position: absolute;
    margin-top: 20%;
    margin-left: 950px;
    background-color: #fff;
}

.img-card3 img{
    margin-top: 5px;
    margin-left: 5px;
    width: 380px;
    height: 170px;
}


/* css for light theme */

.light .img-card1,.light .img-card2,.light .img-card3{
    background-color: #000;
}

@media screen and (max-width : 1920px) {
    .projects .pro-title{
        margin-left: 43%;
    }
    .img-card1{
        width: 450px;
        height: 250px;
        position: absolute;
        margin-top: 15%;
        margin-left: 200px;
    }
    .img-card1 img{
        width: 440px;
        height: 240px;
    }
    .img-card2{
        width: 450px;
        height: 250px;
        margin-top: 15%;
        margin-left: 700px;
    }
    .img-card2 img{
        width: 440px;
        height: 240px;
    }
    .img-card3{
        width: 450px;
        height: 250px;
        margin-top: 15%;
        margin-left: 1200px;
    }
    .img-card3 img{
        width: 440px;
        height: 240px;
    }
}


@media screen and (max-width: 768px){
    .projects{
        width: 100%;
        height:100%;

    }
    .projects .pro-title{
        font-size: 1.4rem;
        text-align: center;
        position:relative;
        margin-left: 0%;
        margin-top: 250%;
    }

    .img-card1{
        margin-top: 20%;
        margin-left: 5%;
        width: 310px;
        height: auto;
    }
    .img-card1 img{
        width: 300px;
        height: 150px;
    }
    .img-card2{
        margin-top: 80%;
        width: 310px;
        height: auto;
        margin-left: 5%;
    }
    .img-card2 img{
        width: 300px;
        height: 150px;
    }
    .img-card3{
        margin-top: 140%;
        margin-left: 5%;
        width: 310px;
        height: auto;
    }
    .img-card3 img{
        width: 300px;
        height: 150px;
    }
}