.social{
    width: 60px;
    height: auto;
    background-color: #000;
    opacity: 60%;
    margin-top: 30%;
    margin-left: 95%;
    border-radius: 10px;
    position: absolute;
    position: fixed;
}

@media screen and (max-width : 768px){
    .social{
        margin-top: 100%;
        margin-left: 85%;
        z-index: 1;
    }
}