.about-container {
    width: 100vh;
    height: 100vh;
}

.about-container .about-title {
    font-size: 1.5rem;
    text-align: center;
    position: relative;
    left: 45%;
    top: 10%;
}

.about-title h1 {
    color: rgb(242, 242, 235);
}

.about-content1 {
    /* width: 100vh; */
    font-size: 1.5rem;
    padding: 5rem 0 0 5rem;

}

p {
    color: #fff;
}

.about-content2 {
    width: 80vh;
    font-size: 1.5rem;
    margin-left: 100%;
}

.about-content2 h3 {
    color: #fff;
    opacity: 70%;
}

.about-content2 p {
    color: #fff;

}

a {
    color: #000;
    font-size: 1.5rem;
}

button {
    font-size: 18px;
    font-weight: bolder;
    display: inline-block;
    outline: 0;
    border: 0;
    cursor: pointer;
    will-change: box-shadow, transform;
    background: radial-gradient(100% 100% at 100% 0%, #89E5FF 0%, #5468FF 100%);
    box-shadow: 0px 0.01em 0.01em rgb(45 35 66 / 40%), 0px 0.3em 0.7em -0.01em rgb(45 35 66 / 30%), inset 0px -0.01em 0px rgb(58 65 111 / 50%);
    padding: 0 2em;
    border-radius: 0.3em;
    color: #fff;
    height: 2.6em;
    text-shadow: 0 1px 0 rgb(0 0 0 / 40%);
    transition: box-shadow 0.15s ease, transform 0.15s ease;
}

button:hover {
    box-shadow: 0px 0.1em 0.2em rgb(45 35 66 / 40%), 0px 0.4em 0.7em -0.1em rgb(45 35 66 / 30%), inset 0px -0.1em 0px #3c4fe0;
    transform: translateY(-0.1em);
}

button:active {
    box-shadow: inset 0px 0.1em 0.6em #3c4fe0;
    transform: translateY(0em);
}

/* css for light theme */

.light .about-title h1{
    color: #080808;
}

.light p{
    color: #080808;
}

.light .about-content2 h3{
    color: #080808;
}


@media screen and (max-width : 1920px) {
    .about-content1 {
        font-size: 1.8rem;
        line-height: 2.8rem;
        text-align: justify;
        padding: 6rem 0 0 8rem;

    }

    .about-content2 {
        width: 80vh;
        font-size: 1.8rem;
        margin-left: 100%;
    }
}

