
.expertise{
    width: 100vh;
    height: 100vh;
}

.expertise .exp-title{
font-size: 1.5rem;
text-align: center;
position: relative;
left: 45%;
top: 10%;
}

.card {
    width: 300px;
    height: 400px;
    background-color: #000;
    position: absolute;
    border-radius: 10px;
    margin-left: 15%;
    margin-top: 150px;
    z-index: 1;
  }
  
  .card2 {
    width: 300px;
    height: 400px;
    background-color: #000;
    position: absolute;
    border-radius: 10px;
    margin-left: 60%;
    margin-top: 150px;
    z-index: 1;
  }

  .tools {
    display: flex;
    align-items: center;
    padding: 9px;
  }
  
  .circle {
    padding: 0 4px;
  }
  
  .box {
    display: inline-block;
    align-items: center;
    width: 10px;
    height: 10px;
    padding: 1px;
    border-radius: 50%;
  }
  
  .red {
    background-color: #ff605c;
  }
  
  .yellow {
    background-color: #ffbd44;
  }
  
  .green {
    background-color: #00ca4e;
  }

.card_content h4 ul li {
    color: #fff;
    font-size: 1rem;
    padding: 1rem 0 1rem 2rem;
}
  
@media screen and (max-width : 1920px) {

  .card{
    width: 400px;
    height: 500px;
    margin-left: 20%;
    margin-top: 200px;
  }
  .card2{
    width: 400px;
    height: 500px;
    margin-left: 60%;
    margin-top: 200px;
  }
  .card_content h4 ul li {
    color: #fff;
    font-size: 1.5rem;
    padding: 2rem 0 2rem 2rem;
}
.expertise .exp-title{
    left:47%
}
}



@media  screen and (max-width: 768px) {
  .expertise{
    width: 100%;
    height: 100%;
}


  .expertise .exp-title{
    font-size: 1.4rem;
    text-align: center;
    position: relative;
    left: 0%;
    margin-top: 50%;
    padding-top: 4rem;
    }

  .card{
    width: 275px;
    height: auto;
    margin-top: 25%;
    margin-left: 10%;
  }
  .card2{
    width: 275px;
    height: auto;
    margin-left: 10%;
    margin-top: 110%;
  }

}
