.container{
    width: 100vh;
    height: 100vh;
}
.myPhoto{
    width: 100%;
    height: 100%;
    object-fit: contain;
}

#image{
    width: 550px;
    height: 550px;
    margin-left: 110%;
    margin-top: 20%;
}

.container .name{
    position: absolute;
    margin-top: 20%;
    color: rgb(242, 242, 235);
    font-size: 13rem;
    text-transform: uppercase;
    text-align: left;
    margin-left: 10%;
    opacity: 50%;
}

.container .content{
    position: absolute;
    margin-top: 35%;
    padding-left: 10rem;
    font-size: 1.5rem;
    color: rgb(37, 172, 4);
}

.aboutme{
    position: absolute;
    margin-top: 35%;
    /* padding-left: 7rem; */
    margin-left: 45%;
}

.aboutme a{
    color: rgb(17, 88, 250);
    font-size: 1.5rem;
}

#space{
    margin-left: 50%;
}



 /* css for light theme */
 .light .name{
    color: #080808;
    opacity: 100%;
}
.light .aboutme a{
    color: rgb(17, 88, 250);
    ;
}


 
  
 @media screen and (max-width : 1920px) {
    #image{
        width: 800px;
        height: 800px;
        margin-left: 100%;
    }
    .container .name{
        margin-top: 15%;
        font-size: 16rem;
        margin-left: 10%;
    }
    .container .content{
        margin-top: 28%;
        padding-left: 12rem;
        font-size: 1.5rem;
    }
    .aboutme{
        margin-top: 28%;
        /* padding-left: 7rem; */
        margin-left: 45.5%;
    }
 }
  
  
  @media only screen and (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) and (min-resolution: 192dpi) {
    /* Add specific styles for Windows */
    .container{
        width: 100vh;
        height: 100vh;
    }
    .myPhoto{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    
    #image{
        width: 650px;
        height: 650px;
        margin-left: 100%;
        margin-top: 15%;
    }
    
    .container .name{
        position: absolute;
        margin-top: 20%;
        color: rgb(242, 242, 235);
        font-size: 15rem;
        text-transform: uppercase;
        text-align: left;
        margin-left: 10%;
        opacity: 50%;
    }
    
    .container .content{
        position: absolute;
        margin-top: 35%;
        padding-left: 10rem;
        font-size: 1.5rem;
        color: rgb(37, 172, 4);
    }
    
    .aboutme{
        position: absolute;
        margin-top: 35%;
        /* padding-left: 7rem; */
        margin-left: 45%;
    }
    
    .aboutme a{
        color: rgb(17, 88, 250);
        font-size: 1.5rem;
    }
    
    #space{
        margin-left: 50%;
    }
  }
  





@media screen and (max-width: 768px){

    .container{
        width: 100%;
        height: auto;
    }

    .myPhoto{
        max-width: 600px;
        min-width: 200px;
        object-fit: contain;
    }
    #image{
        width: 100%;
        height: auto;
        margin-top: 50vh;
        margin-left: 0%;
    }

    .container .name{
        position: absolute;
        margin-top: 55%;
        font-size: 6.5rem;
        text-align: center;
        margin-left:1rem;
        opacity: 50%;
    }
    
    .container .content{
        position: absolute;
        margin-top: 85%;
        font-size: 1rem;
        padding-left: 1rem;
    }
    
    .aboutme{
        position: absolute;
        margin-top: 85%;
        font-size: 1rem;
        margin-left: 70%;
    }
    .aboutme a{
        font-size: 1rem;
    }
    #space{
        margin-left:0%;
    }

}